<template>
  <div class="study">
    <no-jurisdiction
      v-if="
        $store.state.user.mts_user &&
          $store.state.user.mts_user.type !== 'trainee'
      "
    />
    <study v-else />
  </div>
</template>

<script>
import NoJurisdiction from './components/NoJurisdiction'
import Study from './components/Study'
export default {
  components: {
    NoJurisdiction,
    Study
  },
  beforeRouteLeave(to, from, next) {
    if (to.params.name === 'corporate_culture') to.meta.title = '企业文化'
    if (to.params.name === 'service_responsibilities')
      to.meta.title = '健康管理师'
    if (to.params.name === 'workflow') to.meta.title = '工作流程'
    if (to.params.name === 'professional_knowledge')
      to.meta.title = '业务能力知识'
    next()
  }
}
</script>

<style lang="scss">
.study {
  height: 100%;
  .center {
    text-align: center;
  }
  .title {
    font-weight: normal;
  }
}
</style>
